import React, {
  useCallback,
  useEffect,
  useMemo,
  useRef,
  useState,
} from "react";
import styled from "styled-components";
import { CardBody, Text, Skeleton, Button } from "@pancakeswap-libs/uikit";
import Heading from "components/custom/Heading";
import Card from "components/custom/Card";
import PresaleVaultAbi from "config/abi/presaleVault.json";
import presaleVault from "config/constants/presaleVault";
import { useContract } from "hooks/useContract";
import { getAddress } from "utils/addressHelpers";
import { useWeb3React } from "@web3-react/core";
import ConnectWalletButton from "components/ConnectWalletButton";
import { formatted, toNumber } from "utils/formatBalance";

import CountDownTimer from "./Countdown";
import ProgressBar from "./ProgressBar";
import PresaleActionsSection from "./PresaleActionsSection";

const StyledFarmStakingCard = styled(Card)`
  margin-left: auto;
  margin-right: auto;
  width: 100%;
  background-size: 45%;
  background-position: 85% 50%;
  background-repeat: no-repeat;
  max-width: 400px;
  text-align: center;

  @media only screen and (min-width: 792px) {
    background-size: 50%;
    background-position: 45% 70%;
    background-repeat: no-repeat;
  }
`;

const Label = styled.div`
  color: white;
  font-size: 14px;
  margin-top: 20px;
`;

const EthLabel = styled.div`
  font-size: 15px;
  font-weight: 200;
  color: white;
`;

const Row = styled.div`
  display: flex;
  justify-content: space-between;
`;

const StatsBox = styled.div`
  flex: 1;
  text-align: center;
  max-width: 28%;
`;
const BoxLabel = styled(Heading)``;
const BoxText = styled.div`
  font-size: 10px;
`;

const currentTimeStamp = Number(new Date()) + 5000;

const PresaleCard = () => {
  const { account } = useWeb3React();
  /**
   * FETCHED DATA
   */
  // Public Data
  const [totalPresaleTokens, setTotalPresaleTokens] = useState(-1); // useState(100000);
  const [totalRegisteredTokens, setTotalRegisteredTokens] = useState(0); // useState(50000);
  const [presaleOpenTime, setPresaleOpenTime] = useState(0);

  const presalePercentComplete = useMemo(() => {
    return (totalRegisteredTokens / (totalPresaleTokens || 1)) * 100;
  }, [totalPresaleTokens, totalRegisteredTokens]);

  /**
   * INTERNAL STATE
   */
  const [presalePhrase, setPresalePhrase] = useState("");
  const [countdownTimestamp, setCountdownTimestamp] = useState(0);

  const handleCountdownComplete = useCallback(() => {
    let _presalePhrase;
    let _countdownTimestamp;
    if (Number(new Date()) < presaleOpenTime) {
      _presalePhrase = "Opens In";
      _countdownTimestamp = presaleOpenTime;
    } else {
      _presalePhrase = "";
      _countdownTimestamp = 0;
    }
    setPresalePhrase(_presalePhrase);
    setCountdownTimestamp(_countdownTimestamp);
  }, [presaleOpenTime]);

  const presaleContract = useContract(
    PresaleVaultAbi,
    getAddress(presaleVault),
  );
  useEffect(() => {
    handleCountdownComplete();
    // if (2 - 1 === 1) return; // TODO: remove
    const fetchData = async () => {
      const _totalPresaleTokens = await presaleContract.methods
        .totalPresaleTokens()
        .call();
      const _totalRegisteredTokens = await presaleContract.methods
        .totalRegisteredTokens()
        .call();
      const _openTime = await presaleContract.methods.openTime().call();
      
      setTotalPresaleTokens(toNumber(_totalPresaleTokens));
      // console.log("toNumber(_totalPresaleTokens)", toNumber(_totalRegisteredTokens))
      // setTotalRegisteredTokens(toNumber(_totalRegisteredTokens));
      setTotalRegisteredTokens(300000000);
      setPresaleOpenTime(Number(_openTime));
      handleCountdownComplete();
    };
    fetchData();
  }, [presaleContract, account, handleCountdownComplete]);

  return (
    <StyledFarmStakingCard id="lol">
      <CardBody>
        <Heading size="xl" mb="10px" textAlign="center">
          Token Presale
        </Heading>
        <Text
          textAlign="center"
          color="contrast"
          fontWeight={800}
          fontSize="20px"
          mb="5px"
        >
          {totalPresaleTokens !== -1 ? (
            <>
              {formatted(totalRegisteredTokens)} /{" "}
              {formatted(totalPresaleTokens)} Reserved
              <div style={{ marginTop: 10 }}>
                <ProgressBar
                  progressObjects={[
                    {
                      percent: presalePercentComplete,
                      color: "red",
                    },
                  ]}
                />
                <Label style={{ margin: 0 }}>
                  {presalePercentComplete.toFixed(2)}% complete
                </Label>
              </div>
            </>
          ) : (
            <Skeleton animation="pulse" variant="rect" height="20px" />
          )}
        </Text>
        <br />
        <Row>
          <StatsBox>
            <BoxLabel>30%</BoxLabel>
            <BoxText>At launch</BoxText>
          </StatsBox>
          <StatsBox>
            <BoxLabel>5%</BoxLabel>
            <BoxText>Each interval</BoxText>
          </StatsBox>
          <StatsBox>
            <BoxLabel>1.5 days</BoxLabel>
            <BoxText>Vesting interval</BoxText>
          </StatsBox>
        </Row>
        {presalePhrase && (
          <>
            <Text
              color="contrast"
              fontWeight={800}
              fontSize="20px"
              textAlign="center"
              mt="25px"
              mb="0px"
            >
              {presalePhrase}
            </Text>

            <Text fontSize="30px" textAlign="center">
              {Number(new Date()) - countdownTimestamp && (
                <CountDownTimer
                  futureDateTimestamp={countdownTimestamp}
                  onComplete={handleCountdownComplete}
                />
              )}
            </Text>
          </>
        )}
        {!account ? (
          <ConnectWalletButton
            mt="25px"
            width="100%"
            label="Connect Wallet to Proceed"
            disabled={presalePhrase === "Opens In"}
          />
        ) : (
          <PresaleActionsSection disabled={presalePhrase === "Opens In"} />
        )}
      </CardBody>
    </StyledFarmStakingCard>
  );
};

export default PresaleCard;
