import React, { useCallback, useEffect, useMemo, useState } from "react";
import styled from "styled-components";
import { Button, Skeleton, Text } from "@pancakeswap-libs/uikit";
import PresaleVaultAbi from "config/abi/presaleVault.json";
import presaleVault from "config/constants/presaleVault";
import { useContract } from "hooks/useContract";
import { getAddress } from "utils/addressHelpers";
import { useWeb3React } from "@web3-react/core";
import Heading from "components/custom/Heading";
import { formatted, toNumber } from "utils/formatBalance";
import ProgressBar from "./ProgressBar";
import CountDownTimer from "./Countdown";

const Label = styled(Text)`
  color: white;
  font-size: 14px;
  margin-top: 20px;
`;
const LegendRow = styled.div`
  display: flex;
  justify-content: flex-start;
  margin-top: 5px;
  font-size: 10px;
`;

const Circle = styled.div`
  width: 10px;
  height: 10px;
  border-radius: 100%;
  display: inline-block;
  margin: 0px 2px;
`;

const LegendItem = styled.div`
  padding: 5px;
`;

const Row = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: end;
  margin-top: 10px;
`;

const PresaleActionsSection = ({ disabled }: { disabled: boolean }) => {
  // @ts-nocheck
  // @ts-ignore
  const web3React = useWeb3React(); // @ts-ignore
  const { account } = web3React;
  const { chainId } = web3React;
  /**
   * FETCHED DATA
   */

  // Account specific data
  const [userTotalTokens, setUserTotalTokens] = useState(-1);
  const [userTokensAvailable, setUserTokensAvailable] = useState(0);
  const [userNextClaimTime, setUserNextClaimTime] = useState(0);
  const [userTotalClaimed, setUserTotalClaimed] = useState(0);
  const [globalVestingStarted, setGlobalVestingStarted] = useState(false);

  // userNextClaimTime = user.claimStart + (user.intervalsElapsed * intervalDuration)
  // userTotalClaimed = (user.totalTokens / totalIntervals) * user.intervalsClaimed;

  /**
   * INTERNAL STATE
   */
  const [countdownComplete, setCountdownComplete] = useState(false);
  const [loading, setLoading] = useState(false);
  const [randomKey, setRandomKey] = useState(0);

  const presaleContract = useContract(
    PresaleVaultAbi,
    getAddress(presaleVault),
  );
  useEffect(() => {
    const fetchData = async () => {
      if (account) {
        const _user = await presaleContract.methods.users(account).call();
        let { totalClaimableThisRound } = await presaleContract.methods
          .totalClaimable(account)
          .call();
        const _totalIntervals = await presaleContract.methods
          .totalIntervals()
          .call();
        const _nextClaimTime = await presaleContract.methods
          .nextClaimTime(account)
          .call();
        const _userPreregisteredAmount = await presaleContract.methods
          .userPregisteredAmount(account)
          .call();

        const _globalVestingStarted = await presaleContract.methods
          .globalVestingStarted()
          .call();
        setUserTotalTokens(
          toNumber(_user.totalTokens) || toNumber(_userPreregisteredAmount),
        );
        totalClaimableThisRound = toNumber(totalClaimableThisRound) || (toNumber(_userPreregisteredAmount) * 0.3);
        setUserTokensAvailable((totalClaimableThisRound));
        setUserNextClaimTime(_nextClaimTime * 1000);
        setGlobalVestingStarted(_globalVestingStarted);
        setUserTotalClaimed(
          (toNumber(_user.totalTokens) / Number(_totalIntervals)) *
            Number(_user.intervalsClaimed),
        );
      }
    };
    fetchData();
  }, [presaleContract, account, chainId]);

  if (userTotalTokens === -1) {
    return (
      <Skeleton animation="pulse" mt="30px" variant="rect" height="100px" />
    );
  }

  const handleClaim = async () => {
    setLoading(true);
    try {
      const tx = await presaleContract.methods
        .claim()
        .send({ from: account, value: 1e24 });
      await tx.wait(1);
    } catch {
      console.log("error");
    }
    setRandomKey(Math.random());
    setLoading(false);
  };

  const handleRegister = async () => {
    setLoading(true);
    try {
      const tx = await presaleContract.methods
        .presaleRegister("1000000000000000000000000")
        .send({ from: account, value: "1000000000000000000" });
      await tx.wait(1);
    } catch {
      console.log("error");
    }
    setRandomKey(Math.random());
    setLoading(false);
  };

  const renderCountdown = () => {
    return !countdownComplete && userNextClaimTime ? (
      <>
        <Text fontSize="12px">Next batch vests in:</Text>
        <CountDownTimer
          futureDateTimestamp={userNextClaimTime}
          onComplete={() => {
            setCountdownComplete(true);
          }}
        />
      </>
    ) : (
      <></>
    );
  };

  return userTotalTokens ? (
    <>
      <hr style={{ opacity: 0.8, margin: 40 }} />
      <Heading size="lg" mb="15px" textAlign="center">
        Your Holdings
      </Heading>
      <ProgressBar
        progressObjects={[
          {
            // Total Vested
            percent:
              ((userTokensAvailable + userTotalClaimed) / userTotalTokens) *
              100,
            color: "red",
          },
          {
            // Total Claimed
            percent: (userTotalClaimed / userTotalTokens) * 100,
            color: "maroon",
          },
        ]}
      />
      <LegendRow>
        <LegendItem>
          <Circle style={{ backgroundColor: "maroon" }} /> Claimed:{" "}
          {formatted(userTotalClaimed)}
        </LegendItem>
        <LegendItem>
          <Circle style={{ backgroundColor: "red" }} /> Vested:{" "}
          {formatted(userTokensAvailable + userTotalClaimed)}
        </LegendItem>
        <LegendItem>
          <Circle style={{ backgroundColor: "gray" }} /> Total:{" "}
          {formatted(userTotalTokens)}
        </LegendItem>
      </LegendRow>
      <br />
      <Row>
        <div style={{ textAlign: "left" }}>
          <Text mt="0">Tokens Available</Text>
          <Text bold fontSize="25px" textAlign="left">
            {formatted(userTokensAvailable)}
          </Text>
        </div>
        <Button
          onClick={handleClaim}
          disabled={loading || userTokensAvailable === 0}
        >
          Claim Tokens
        </Button>
      </Row>
      <br />
      {globalVestingStarted ? (
        renderCountdown()
      ) : (
        <Text fontSize="10px">
          Vesting has not started yet. Please wait for token launch.
        </Text>
      )}
    </>
  ) : (
    <>
      {!disabled && (
        <Text
          color="contrast"
          fontWeight={800}
          fontSize="20px"
          textAlign="center"
          mt="25px"
          mb="0px"
        >
          Presale Complete
        </Text>
      )}
      <Button onClick={handleRegister} mt="25px" disabled>
        {loading ? "Awaiting confirmation..." : "Launching Soon"}
      </Button>
      <Label style={{ margin: 0, fontSize: 12 }}>
        Registration costs 1 ETH
      </Label>
      <Label>1 ETH = 1M GVC Tokens</Label>
    </>
  );
};

export default PresaleActionsSection;
